import React, { FC } from "react";
import { useGetLocale } from "providers";

import { History, UserStats } from "pages/Affiliate/types";
import { Activity } from "./components/Activity";
import css from "./style.module.scss";
import { track } from "utils";

interface Props {
  info: UserStats | undefined;
  history: History | undefined;
}

export const AffiliateHistory: FC<Props> = ({ info, history }) => {
  const intl = useGetLocale();

  try {
    return (
      <div className={css.wrapper}>
        <div className={css.paddingBox}>
          <div className={css.titleBox}>
            <h1 className={css.title}>
              {intl.formatMessage({ id: "affiliate.history.title" })}
            </h1>
            <span className={css.moneyWrapper}>
              <span className={css.valueWrapper}>
                <span className={css.value}>{info?.total_earned || 0}</span>
              </span>
              <span className={css.money}>USD</span>
            </span>
          </div>

          <div className={css.content}>
            <h1 className={css.title}>
              {intl.formatMessage({ id: "affiliate.history.tableTitle" })}
            </h1>

            <div className={css.activityWrapper}>
              <Activity data={history?.data || []} />
            </div>
          </div>
        </div>
      </div>
    );
  } catch (error) {
    track()?.errors(error, {
      custom: {
        path: "AffiliateHistory",
      },
    });
    return null;
  }
};
