import React, { useEffect, useRef } from "react";
import { Helmet } from "react-helmet";

import { useFetch, useLocalStorage } from "hooks";

import { MainContent } from "./components/MainContent";
import { AffiliateHistory } from "./components/SecondaryContent";

import { History, RatesType, UserStats } from "./types";

import css from "./style.module.scss";
import "config/themes/_affiliate.scss";

const Affiliate = () => {
  const [token] = useLocalStorage("token");

  const [infoFetch, infoData] = useFetch<UserStats>(
    "/statistics/affiliate-info",
  );

  const [historyFetch, historyData] = useFetch<History>(
    "/statistics/affiliate-data",
  );

  const [ratesFetch, ratesData] = useFetch<RatesType>(
    "/statistics/affiliate-rates",
  );

  useEffect(() => {
    const vars = {
      variables: { token },
    };

    infoFetch(vars);
    historyFetch(vars);
    ratesFetch(vars);
  }, [token]);

  const loading = infoData.loading || historyData.loading || ratesData.loading;

  return (
    <>
      <Helmet>
        <title>Affiliate | Crypto Gambling Casino</title>
        <meta name="title" content="Affiliate - Pasino.com" />
        <meta
          name="description"
          content="Invite your friends and earn with every bet a passive income.  "
        />
        <meta
          name="keywords"
          content="Pasino Affiliate, Pasino Invite and Earn, Pasino Friends, Pasino Ref System "
        />
        <meta name="robots" content="index, follow" />
        <meta http-equiv="Content-Type" content="text/html; charset=utf-8" />
        <meta name="language" content="English" />
      </Helmet>
      <div className={css.root}>
        <MainContent
          loading={loading}
          infoData={infoData}
          ratesData={ratesData}
        />
        <AffiliateHistory info={infoData?.data} history={historyData?.data} />
      </div>
    </>
  );
};

export default Affiliate;
