import React, { useRef } from "react";

import { useGetLocale } from "providers";

import { ReactComponent as FacebookIcon } from "assets/icons/facebook-mui.svg";
import { ReactComponent as FileCopyIcon } from "assets/icons/file-copy.svg";
import { ReactComponent as TwitterIcon } from "assets/icons/twitter.svg";
import { ReactComponent as GetAppIcon } from "assets/icons/get-app.svg";

import { Button, Input, notifyBarManager } from "components";
import { useWindowSize } from "providers";

import { UserStats } from "pages/Affiliate/types";
import { copy } from "utils/copyToClipboard";

import generalCss from "../../style.module.scss";
import css from "./style.module.scss";

interface Props {
  data: UserStats | undefined;
}

const zeroData = {
  user_id: null,
};

export const MyStatistics = ({ data }: Props) => {
  const { user_id } = data ? data : zeroData;

  const intl = useGetLocale();
  const { width } = useWindowSize();
  const inputRef = useRef<HTMLInputElement>(null);
  const crashInputRef = useRef<HTMLInputElement>(null);

  const setCopied = (isCopy: boolean) => {
    if (isCopy) {
      notifyBarManager.success({
        timeOut: 2000,
        message: "Copied",
      });
    } else {
      notifyBarManager.error({
        timeOut: 2000,
        message: "Error",
      });
    }
  };

  const handleCopyToBuffer = (
    props: any,
    ref: React.RefObject<HTMLInputElement> | null,
  ) => {
    if (props && props.target) {
      const value: string = props.target?.value;
      copy(value, setCopied, ref?.current || undefined);
      props.target.select();
    } else if (typeof props === "string" && ref?.current) {
      copy(props, setCopied, ref.current);
    } else {
      setCopied(false);
    }
  };

  const share = (url: string) => {
    return !window.open(
      url,
      "",
      `width=640,height=580,left=${width / 2},top=200`,
    );
  };

  const invitationURL = `${process.env.REACT_APP_FRONT_URL}${
    user_id ? `/?user_id=${user_id}` : ""
  }`;

  const crashInvitationURL = `${process.env.REACT_APP_FRONT_URL}/crash-promo${
    user_id ? `?user_id=${user_id}` : ""
  }`;

  return (
    <div className={generalCss.part}>
      <div className={generalCss.subWrapper}>
        <div className={generalCss.subNumber}>1</div>
        <div className={generalCss.subContent}>
          <h2 className={generalCss.subTitle}>
            {intl.formatMessage({ id: "affiliate.statistics.title" })}
          </h2>
          <p className={generalCss.text}>
            {intl.formatMessage({ id: "affiliate.statistics.titleText" })}
          </p>
        </div>
      </div>

      <div className={generalCss.content}>
        <div className={css.copyInput}>
          <Input
            ref={inputRef}
            onClick={(event) => {
              handleCopyToBuffer(event, inputRef);
            }}
            readOnly
            value={invitationURL}
            className={css.input}
          />

          <Input
            ref={crashInputRef}
            onClick={(event) => {
              handleCopyToBuffer(event, crashInputRef);
            }}
            readOnly
            value={crashInvitationURL}
            className={css.input}
          />
        </div>

        <div className={css.flexContent}>
          <Button
            icon
            className={`${generalCss.button} ${generalCss.mainButton} ${css.icon}`}
            onClick={() => {
              handleCopyToBuffer(invitationURL, inputRef);
            }}>
            <FileCopyIcon />
            <span className={css.iconText}>
              {intl.formatMessage({ id: "affiliate.statistics.buttons.copy" })}
            </span>
          </Button>

          <Button
            icon
            onClick={() => {
              const twitterLink = `https://twitter.com/intent/tweet?text=${`${intl.formatMessage(
                { id: "affiliate.statistics.buttons.tweetLinkText" },
              )} ${invitationURL}`}`;

              share(twitterLink);
            }}
            className={`${generalCss.button} ${generalCss.mainButton} ${css.icon}`}>
            <TwitterIcon />
            <span className={css.iconText}>
              {intl.formatMessage({ id: "affiliate.statistics.buttons.tweet" })}
            </span>
          </Button>

          <Button
            icon
            onClick={() => {
              const facebookLink = `https://www.facebook.com/sharer/sharer.php?u=${invitationURL}&amp;src=sdkpreparse`;
              share(facebookLink);
            }}
            className={`${generalCss.button} ${generalCss.mainButton} ${css.icon}`}>
            <FacebookIcon />
            <span className={css.iconText}>
              {intl.formatMessage({
                id: "affiliate.statistics.buttons.facebook",
              })}
            </span>
          </Button>

          <form action="banners.zip">
            <Button
              icon
              type="submit"
              className={`${generalCss.button} ${generalCss.mainButton} ${css.icon}`}>
              <GetAppIcon />
              <span className={css.iconText}>
                {intl.formatMessage({
                  id: "affiliate.statistics.buttons.download",
                })}
              </span>
            </Button>
          </form>
        </div>
      </div>
    </div>
  );
};
