import React, { FC } from "react";

import pageStyles from "../../style.module.scss";
import { useLocalStorage } from "hooks";
import Select from "./components/Select";

export const RedeemRewards: FC = () => {
  const [token] = useLocalStorage("token");

  return (
    <div className={pageStyles.part}>
      <div className={pageStyles.subWrapper}>
        <div className={pageStyles.subNumber}>3</div>
        <div className={pageStyles.subContent}>
          <h2 className={pageStyles.subTitle}>{"Redeem Rewards"}</h2>
          <p className={pageStyles.text}>
            {
              "Transfer your referral rewards balance to your main account balance"
            }
          </p>
        </div>
      </div>

      {token ? (
        <Select />
      ) : (
        <p className={pageStyles.text}>
          {"You need to be logged in to withdraw your referral rewards."}
        </p>
      )}
    </div>
  );
};
