import React, { CSSProperties, FC, useEffect, useState } from "react";
import { useGetLocale } from "providers";

import { useFetch, useLocalStorage } from "hooks";
import { Icon, Pagination } from "components";

import { Refers } from "pages/Affiliate/types";
import { Dialog } from "containers/PopUps";

import { Activity } from "./components/Activity";
import css from "./style.module.scss";

import { Scrollbar } from "components/Scrollbar";

interface Props {
  opened: boolean;
  total: string;
  onClose: () => void;
}

const titleStyles: CSSProperties = {
  width: "100%",
  textAlign: "center",
  fontSize: "1.5rem",
  marginTop: 30,
};

const paperProps = {
  className: css.root,
};
const paginationClasses = {
  root: css.paginationRoot,
  button: css.paginationButton,
  activePage: css.paginationActive,
};
export const InvitedUsers: FC<Props> = ({ opened, total, onClose }) => {
  const intl = useGetLocale();
  const [token] = useLocalStorage("token");

  const [userList, setUserList] = useState<Refers | null>(null);

  const [referredFetch, referredData] = useFetch<Refers>(
    "/statistics/referred-users",
  );

  const refFetch = (page: number) => {
    if (token) {
      referredFetch({
        variables: {
          token,
          page,
        },
      });
    }
  };

  const [refPage, setRefPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);

  const handlePageChange = (page: number) => {
    if (!referredData.loading) {
      setRefPage(page);
      refFetch(page);
    }
  };

  useEffect(() => {
    if (opened && !userList) {
      refFetch(refPage);
    }
  }, [opened]);

  useEffect(() => {
    if (referredData.data) {
      setUserList(referredData.data);
      setRefPage(referredData.data.current_page);
      setTotalPages(referredData.data.total_pages);
    }
  }, [referredData]);

  return (
    <Dialog
      onClose={onClose}
      isOpen={opened}
      paper={paperProps}
      withCloseIcon
      closeIcon={(props) => (
        //@ts-ignore
        <Icon variant="close" className={css.closeIcon} {...props} />
      )}>
      <div className={css.titleBox}>
        <h1 className={css.title}>
          {intl.formatMessage({ id: "affiliate.invites.userList.title" })}
        </h1>
        <span className={css.valueWrapper}>
          <span className={css.value}>{total}</span>
        </span>
        <span className={css.money}>USD</span>
      </div>

      <div className={css.paginationBlock}>
        <Pagination
          page={refPage}
          pageAmount={totalPages}
          maxButtonVisible={3}
          showFirstLastPage
          showPrevNextButton
          onChange={handlePageChange}
          disabled={referredData.loading}
          classes={paginationClasses}
        />
      </div>

      <Scrollbar withoutShadow>
        <div className={css.content}>
          <Activity data={userList?.referred_users || []} />
          {(!userList ||
            (userList && userList.referred_users.length === 0)) && (
            <div className={css.title} style={titleStyles}>
              {intl.formatMessage({ id: "wallet.emptyTable" })}
            </div>
          )}
        </div>
      </Scrollbar>
    </Dialog>
  );
};
