import React, { CSSProperties } from "react";
import { useGetLocale } from "providers";

import { GetCoinImg, Table } from "components";
import { getIntlValue } from "providers";

import { Operation } from "pages/Affiliate/types";

import css from "./style.module.scss";

interface Props {
  data: Operation[];
}

const alertStyle: CSSProperties = {
  width: "100%",
  textAlign: "center",
  fontSize: "1.5rem",
  marginTop: 30,
};

export const Activity = ({ data }: Props) => {
  const intl = useGetLocale();
  const formatMessage = getIntlValue(intl, "affiliate.history.table.");

  return (
    <div className={css.root}>
      <Table
        root={{
          className: css.tableContainer,
        }}
        head={{
          values: [formatMessage("coin"), formatMessage("earned")],
          classes: {
            th: css.tCell,
            thead: css.tHead,
          },
        }}
        body={{
          rows: data.map(({ coin, amount_earned }, i) => {
            return {
              values: [
                coin,
                <span className={css.contentWrapper}>
                  <GetCoinImg coin={coin} className={css.coinImg} />
                  {amount_earned}
                </span>,
              ],
              key: `${coin}_${amount_earned}__${i}`,
            };
          }),
          classes: {
            tbody: css.tBody,
            td: css.tContent,
          },
        }}
      />

      {data.length === 0 && (
        <div className={css.alert} style={alertStyle}>
          {intl.formatMessage({ id: "wallet.emptyTable" })}
        </div>
      )}
    </div>
  );
};
