import React from "react";
import { useGetLocale } from "providers";

import { CircularProgress } from "components";
import { MutationResult } from "hooks";

import { RatesType, UserStats } from "pages/Affiliate/types";

import { RedeemRewards } from "./components/redeemRewards";
import { MyStatistics } from "./components/myStatistics";
import { Invites } from "./components/Invites";
import { Rates } from "./components/Rates";
import css from "./style.module.scss";
import { track } from "utils";

interface Props {
  loading: boolean;
  infoData: MutationResult<UserStats>;
  ratesData: MutationResult<RatesType>;
}

export const MainContent = ({ loading, infoData, ratesData }: Props) => {
  const intl = useGetLocale();

  try {
    return (
      <div className={css.wrapper}>
        <div className={css.titleWrapper}>
          <div className={css.titleContent}>
            <h1 className={css.title}>
              {intl.formatMessage({ id: "affiliate.title" })}
            </h1>
            <p className={css.text}>
              {intl.formatMessage({ id: "affiliate.subtitle" })}
            </p>
          </div>
        </div>
        {loading ? (
          <div className={css.loader}>
            <CircularProgress color="white" />
          </div>
        ) : (
          <>
            <MyStatistics data={infoData.data} />
            <Rates data={ratesData.data} />
            <RedeemRewards />
            <Invites total={infoData.data} />
          </>
        )}
      </div>
    );
  } catch (err) {
    track()?.errors(err, {
      custom: {
        path: "MainContent",
      },
    });

    return null;
  }
};
