import React, { useMemo } from "react";
import { useGetLocale } from "providers";

import { RatesType } from "pages/Affiliate/types";

import gCss from "../../style.module.scss";
import css from "./style.module.scss";

interface Props {
  data: RatesType | undefined;
}

const gameReg = /(\w+)_referral_rate/;

export const Rates = ({ data }: Props) => {
  const intl = useGetLocale();

  const games = useMemo(() => {
    if (data) {
      return Object.keys(data).map((game) => {
        const match = game.match(gameReg);
        if (match) {
          return match[1];
        }
        return "";
      });
    }

    return [];
  }, [data]);

  return (
    <div className={gCss.part}>
      <div className={gCss.subWrapper}>
        <div className={gCss.subNumber}>2</div>
        <div className={gCss.subContent}>
          <h2 className={gCss.subTitle}>
            {intl.formatMessage({ id: "affiliate.rates.title" })}
          </h2>
          <p className={gCss.text}>
            {intl.formatMessage({ id: "affiliate.rates.titleText" })}
          </p>
        </div>
      </div>

      <div className={`${gCss.content} ${css.wrapper}`}>
        {games.map((value: string, i) => {
          if (!data || !value) {
            return null;
          }

          return (
            <div className={css.rewardField} key={`${value}_${i}`}>
              <h2 className={gCss.subTitle}>
                {intl.formatMessage({ id: `affiliate.rates.${value}` })}
              </h2>

              <p className={css.smallText}>
                {intl.formatMessage({
                  id: `affiliate.rates.text_p1`,
                })}
                <span>{` ${data[`${value}_referral_rate`]}% `}</span>
                {intl.formatMessage({
                  id: `affiliate.rates.text_p2`,
                })}
              </p>
            </div>
          );
        })}
      </div>
    </div>
  );
};
