import React from "react";
import clsx from "clsx";
import { useGetLocale } from "providers";

import { Table } from "components";
import { getIntlValue } from "providers";

import { RefUser } from "pages/Affiliate/types";

import css from "./style.module.scss";

interface Props {
  data: RefUser[];
}

export const Activity = ({ data }: Props) => {
  const intl = useGetLocale();
  const formatMessage = getIntlValue(intl, "affiliate.invites.userList.table.");

  const preparedData = data.sort((a, b) => {
    if (Number(a.total_wagered) > Number(b.total_wagered)) {
      return -1;
    } else if (Number(a.total_wagered) < Number(b.total_wagered)) {
      return 1;
    }

    return 0;
  });

  return (
    <div className={css.root}>
      <Table
        root={{
          className: css.table,
        }}
        head={{
          values: [
            formatMessage("user"),
            formatMessage("date"),
            formatMessage("bets"),
            formatMessage("total"),
          ],
          classes: {
            th: clsx(css.tHead, css.tCell),
          },
        }}
        body={{
          rows: preparedData.map(
            ({ bets, registration_date, user_name, total_wagered }) => {
              return {
                values: [
                  user_name,
                  registration_date,
                  bets,
                  "USD " + total_wagered,
                ],
                key: registration_date + user_name,
              };
            },
          ),
          classes: {
            td: css.tContent,
          },
        }}
      />
    </div>
  );
};
