import React, { FC, useState } from "react";
import { useGetLocale } from "providers";
import { ReactComponent as AccountCircleIcon } from "assets/icons/account-circle.svg";
import clsx from "clsx";

import { Button } from "components";
import { UserStats } from "pages/Affiliate/types";

import { InvitedUsers } from "./InvitedUsers";
import gCss from "../../style.module.scss";
import css from "./style.module.scss";

interface Props {
  total: UserStats | undefined;
}

export const Invites: FC<Props> = ({ total }) => {
  const intl = useGetLocale();

  const [isModalOpened, setIsModalOpened] = useState(false);

  const openUserModal = () => {
    setIsModalOpened(true);
  };

  const closeUserModal = () => {
    setIsModalOpened(false);
  };

  return (
    <div className={gCss.part}>
      <div className={gCss.subWrapper}>
        <div className={gCss.subNumber}>4</div>
        <div className={gCss.subContent}>
          <h2 className={gCss.subTitle}>
            {intl.formatMessage({ id: "affiliate.invites.title" })}
          </h2>
          <p className={gCss.text}>
            {intl.formatMessage({ id: "affiliate.invites.titleText" })}
          </p>
        </div>
      </div>

      <div className={gCss.content}>
        <div className={css.invitesBox}>
          <div className={css.invitesTitle}>
            <AccountCircleIcon className={css.invitesLogo} />
            <span className={css.invitesTitleText}>
              {intl.formatMessage({ id: "affiliate.invites.you" })}
            </span>
          </div>

          <div className={css.levelContentBox}>
            <h1 className={clsx(gCss.subTitle, css.title)}>
              {`${intl.formatMessage({
                id: "affiliate.invites.level",
              })}`}
            </h1>

            <p className={`${gCss.text} ${css.levelText}`}>
              {`${total ? total.referred_users : "0"} ${intl.formatMessage({
                id: "affiliate.invites.players",
              })}`}
            </p>

            <Button
              onClick={openUserModal}
              className={`${gCss.button} ${gCss.mainButton} ${css.levelButton}`}>
              {intl.formatMessage({ id: "affiliate.invites.view" })}
            </Button>
          </div>

          <div className={css.line} />
          <InvitedUsers
            total={total ? total.total_earned : "0"}
            opened={isModalOpened}
            onClose={closeUserModal}
          />
        </div>
      </div>
    </div>
  );
};
