import React, { useEffect, useState } from "react";

import { Button, CircularProgress, notifyBarManager } from "components";
import { useCoins, useFetch, useLocalStorage } from "hooks";
import { toDecimal } from "utils";

import { modalsLoaderColor } from "sass/styleVariables";
import RakeSelect from "pages/ModalPages/pages/Bonuses/components/RakeSelect";
import css from "./style.module.scss";

interface CoinBalance {
  name: string;
  amount: string;
  symbol: string;
}

interface AffiliateBalancesResponseData {
  message: string;
  balances: CoinBalance[];
}

const CoinSelect = () => {
  const { selectedCoin } = useCoins("selectedCoin");
  const [token] = useLocalStorage("token");

  const [rakeCoins, setRakeCoins] = useState<CoinBalance[]>([
    { name: "Tron", symbol: "TRX", amount: "0.00000000" },
    { name: "Nano", symbol: "NANO", amount: "0.00000000" },
    { name: "Tether", symbol: "USDT", amount: "0.00000000" },
    { name: "Feyorra", symbol: "FEY", amount: "0.00000000" },
    { name: "Bitcoin", symbol: "BTC", amount: "0.00000000" },
    { name: "Ethereum", symbol: "ETH", amount: "0.00000000" },
    { name: "Litecoin", symbol: "LTC", amount: "0.00000000" },
    { name: "Dogecoin", symbol: "DOGE", amount: "0.00000000" },
    { name: "Solana", symbol: "SOL", amount: "0.00000000" },
  ]);
  const [activeAffiliateCoin, setAffiliateCoin] = useState<string>(
    selectedCoin?.coin && selectedCoin.coin !== "GEM"
      ? selectedCoin.coin
      : rakeCoins[0].symbol,
  );

  const [affiliateBalanceFetch, affiliateBalanceData] =
    useFetch<AffiliateBalancesResponseData>("affiliate/get-balances");

  useEffect(() => {
    if (token) affiliateBalanceFetch();
  }, [token]);

  useEffect(() => {
    if (affiliateBalanceData.data) {
      setRakeCoins(
        affiliateBalanceData.data.balances.filter(
          (coin) => coin.symbol !== "GEM",
        ),
      );
    }
  }, [affiliateBalanceData.data]);

  const [balanceFetch, balanceData] = useFetch<any>(
    "affiliate/withdraw-balance",
  );

  const handleAffiliateWithdraw = async () => {
    const result = await balanceFetch({
      variables: {
        coin: activeAffiliateCoin,
      },
    });
    if (result.data) {
      const isError = !!result.error || !result.data?.success;
      notifyBarManager[isError ? "error" : "success"]({
        timeOut: 4000,
        message: result.data.message,
      });
    }

    affiliateBalanceFetch();
  };

  const handleAffiliateWithdrawAll = async () => {
    for (const coin of rakeCoins) {
      if (toDecimal(coin.amount).gt(0)) {
        const result = await balanceFetch({
          variables: {
            coin: coin.symbol,
          },
        });
        if (result.data) {
          const isError = !!result.error || !result.data?.success;
          notifyBarManager[isError ? "error" : "success"]({
            timeOut: 4000,
            message: result.data.message,
          });
        }
      }
    }

    affiliateBalanceFetch();
  };

  return (
    <>
      <RakeSelect
        options={rakeCoins}
        value={activeAffiliateCoin}
        onChange={(value) => setAffiliateCoin(value || "")}
      />

      <div className={css.buttons}>
        <Button
          disabled={balanceData.loading || !token}
          classes={{
            root: css.button,
          }}
          onClick={handleAffiliateWithdraw}>
          {balanceData.loading ? (
            <CircularProgress
              className={css.loader}
              color={modalsLoaderColor}
              size={50}
            />
          ) : (
            "withdraw"
          )}
        </Button>
        <Button
          disabled={balanceData.loading || !token}
          classes={{
            root: css.button,
          }}
          onClick={handleAffiliateWithdrawAll}>
          {balanceData.loading ? (
            <CircularProgress
              color={modalsLoaderColor}
              className={css.loader}
              size={50}
            />
          ) : (
            "all"
          )}
        </Button>
      </div>
    </>
  );
};

export default CoinSelect;
